<template>
  <div class="section">
      <el-date-picker
        v-model="date"
        type="date"
        format="yyyy 年 MM 月 dd 日"
        value-format="yyyy-MM-dd"
        @change="getData"
        placeholder="选择日期">
        </el-date-picker>
       <table > 
          <tr>
              <td rowspan="2">交班时间</td>
              <td rowspan="2">钻井液类型</td>
              <td rowspan="2">密度g/cm3</td>
              <td rowspan="2">漏斗粘度s</td>
              <td rowspan="2">含砂量%</td>
              <td rowspan="2">固含%</td>
              <td rowspan="2">pH值</td>
              <td rowspan="2">滤饼厚度mm</td>
              <td >常温</td>
              <td >高温</td>
              <td rowspan="2">塑性粘度（mPa.s）</td>
              <td rowspan="2">动切力（Pa）</td>
              <td rowspan="2">流性指数n值</td>
              <td rowspan="2">稠度系数K值（Pa.sn）</td>
              <td rowspan="2">油/水比</td>
              <td rowspan="2">破乳电压（v）</td>
              <td rowspan="2">碱度（pom）ml</td>
          </tr>
          <tr>
              <td colspan="2">滤失量（mL）</td>
          </tr>
          <tr v-for="(item,i) in info.fluid_data_list" :key="i">
              <td>{{item.daily_at}}</td>
              <td>{{item.fluid_type}}</td>
              <td>{{item.density}}</td>
              <td>{{item.viscosity}}</td>
              <td>{{item.sand_ratio}}</td>
              <td>{{item.solid_ratio }}</td>
              <td>{{item.ph}}</td>
              <td>{{item.cake_thickness}}</td>
              <td>{{item.fluid_loss}}</td>
              <td>{{item.ht_fluid_loss}}</td>
              <td>{{item.plastic_viscosity}}</td>
              <td>{{item.yield_value}}</td>
              <td>{{item.nvalue}}</td>
              <td>{{item.kvalue}}</td>
              <td>{{item.oil_water_ratio}}</td>
              <td>{{item.emulsion_breaking_voltage}}</td>
              <td>{{item.alkalinity}}</td>
          </tr>
          <tr style="height: 80px;">
              <td colspan="6">泥浆泵</td>
              <td colspan="7">固控设备</td>
              <td colspan="4">钻完井液材料用量统计</td>
          </tr>
          <tr>
              <td>泥浆泵号</td>
              <td>泥浆泵型号</td>
              <td>冲程(in/ mm)</td>
              <td>缸套尺寸(in/ mm)</td>
              <td colspan="2">每冲排量(L/S)</td>
              <td>设备名称</td>
              <td>入口密度(g/cm3)</td>
              <td>出口密度(g/cm3)</td>
              <td>排量(L/S)</td>
              <td>使用时间(h)</td>
              <td>筛布张数</td>
              <td>筛布目数</td>
              <td>材料名称  </td>
              <td>当日用量</td>
              <td>当日补充量</td>
              <td>剩余量</td>
          </tr>
          <tr>
              <td>{{info.pump_no}}</td>
              <td>{{info.pump_model}}</td>
              <td>{{info.pump_stroke}}</td>
              <td>{{info.pump_sleeve_size}}</td>
              <td colspan="2">{{info.pump_volume}}</td>
              <td>{{info.device_name}}</td>
              <td>{{info.device_density_in}}</td>
              <td>{{info.device_density_out}}</td>
              <td>{{info.device_volume}}</td>
              <td>{{info.device_use_time}}</td>
              <td>{{info.device_cloth_count1}}</td>
              <td>{{info.device_cloth_count2}}</td>
              <td>{{info.material_name}}</td>
              <td>{{info.material_usage}}</td>
              <td>{{info.material_supply}}</td>
              <td>{{info.material_remain}}</td>
          </tr>
          <tr>
              <td colspan="9">应急材料储备量</td>
              <td rowspan="2" colspan="8">钻井液处理记录</td>
          </tr>
          <tr>
              <td colspan="2">水泥(T)</td>
              <td colspan="2">重浆(m3)</td>
              <td colspan="3">堵漏材料(T)</td>
              <td colspan="2">柴油(m3)</td>
          </tr> 
         <tr>
              <td colspan="2">{{info.cement || 'null'}}</td>
              <td colspan="2">{{info.heavy_mud}}</td>
              <td colspan="3">{{info.plugging}}</td>
              <td colspan="2">{{info.diesel}}</td>
              <td colspan="8">{{info.record}}</td>
          </tr>
          
      </table>
  </div>
</template>

<script>
import request from "@/utils/request";
export default {
  props: ["route"],
  data () {
     return {
      date: '2018-07-20',
      info: {}
    };
  },

  components: {},

  computed: {},

  mounted: function() {
    this.getData()
  },

  methods: {
    getData() {
        let data = this.route;
        request({
        url: "/report/drillingfluid/daily?",
        method: "get",
        params: {
            ...data,
            date:this.date //'2018-07-20'
        }
        }).then(res => {
            this.info = res;
        });
    }
  }
}
</script>




